
.skill-map {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 50px;
    flex-wrap: wrap;
    gap: 40px;
  }
  .skill img {
    max-width: 70px;
    transition: 0.2s all ease-in-out;
  }
  .skill img:hover {
    transform: scale(1.3);
  }
  .skill {
    padding: 10px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(5, 0, 0, 0.3);
    box-shadow: 0 8px 32px 0 rgba(135, 47, 31, 0.37);
    backdrop-filter: blur(6px);
    -webkit-backdrop-filter: blur(6px);
    border: 1px 0 solid rgba(255, 255, 255, 0.18);
    border-radius: 10px;
    width: 180px;
    height: 110px;
  }
  
@media (max-width: 768px) {
    .skill-map {
      margin: 10px;
      margin-top: 50px;
      gap: 15px;
    }
    .skill {
      width: 100px;
      height: 80px;
    }
    .skill img {
      max-width: 50px;
    }
  }